html, body {
	height: 100%;
}

body {
	display: flex;
	margin: 0;
	align-items: center;
	justify-content: center;
	background: black;

	.cats {
		display: grid;
		grid-auto-flow: row;
		grid-auto-columns: 128px;
		grid-auto-rows: 128px;
		overflow: hidden;
		margin: auto;

		video {
			width: 128px;
			height: 128px;
		}
	}
}

.start {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	display: flex;
	justify-self: center;
	align-self: center;
	justify-content: center;
	align-items: center;
	margin: 0;
	width: 100%;
	height: 100%;
	background: #fff7;
	position: fixed;
	top: 0;
	left: 0;
}